import {
  playerRadius,
  canvasSize as canv
} from './gameSettings'
const c = document.getElementById('root').getContext('2d');


export default function(state) {
  c.clearRect(0, 0, canv.width, canv.height)
  drawPlayers(state.myself, state.enemy)
  // drawBall(state.ball)
  c.closePath()
}



// function drawBall(ball) {
//   c.beginPath()
//   c.arc(
//     ball.position[0],
//     ball.position[1],
//     ball.radius, 0, 2 * Math.PI)
//   c.fillStyle = 'black'
//   c.fill()
// }

function drawPlayers(myself, enemy) {
  if (myself && myself.position) {
    c.beginPath()
    c.arc(
      myself.position[0],
      canv.height - playerRadius - myself.position[1],
      playerRadius, 0, 2 * Math.PI)
    c.fillStyle = 'black'
    c.fill()
  }

  if (enemy && enemy.position) {
    c.beginPath()
    c.arc(
      enemy.position[0],
      canv.height - playerRadius - enemy.position[1],
      playerRadius, 0, 2 * Math.PI)
    c.fillStyle = '#ccc'
    c.fill() 
  }
}





