import {
  yDampingOnRaise,
  yAccelerationOnFall,
  jumpingImpulse,
  gravity,
  playerFlatteningOnFall,
} from '../gameSettings'

export default function({
  myself: {
    position: [xPos, yPos],
    velocity: [xVel, yVel]
  },
  jumping
}) {

  const dir = getDirection(yVel)
  const positionRelativeToBottom = getPos(yPos)

  if (positionRelativeToBottom.match(/BELOW_BOTTOM|ON_BOTTOM/) && jumping) {
    return jumpingImpulse }

  switch (dir) {

    case 'RESTING': // REACHED MAX POINT OF 'FLATTENING' - MUST JUMP BACK TO 0
      if (yPos <= -playerFlatteningOnFall) return yVel + 2
      return yVel

    case 'GOING_UP':
      switch (positionRelativeToBottom) {
        case 'ABOVE_BOTTOM':
          if (jumping) {
            return yVel - gravity * yDampingOnRaise 
          }
          return (yVel - gravity*3) * yDampingOnRaise
        case 'BELOW_BOTTOM': // PLAYER 'FLATTENED' AFTER FALL, RETURNING TO 0
          if (yPos + yVel > 0) return Math.abs(yPos)
          return yVel
        case 'ON_BOTTOM': return 0 // COMPLETELY RETURNED TO 0 AFTER FLATTENINIG
        default: return 0
      }

    case 'GOING_DOWN':
      yVel -= gravity
      yVel *= yAccelerationOnFall;

// flying down, should I stop?
      if (yPos + yVel < -playerFlatteningOnFall) {
        yVel = -(yPos + playerFlatteningOnFall)
      }
      return yVel

    default: return yVel
  }

}


function getDirection(yVel) {
  if (yVel === 0) return 'RESTING'
  if (yVel > 0) return 'GOING_UP'
  return 'GOING_DOWN'
}

function getPos(yPos) {
  if (yPos > 0) return 'ABOVE_BOTTOM'
  if (yPos < 0) return 'BELOW_BOTTOM'
  return 'ON_BOTTOM'
}


