import { canvasSize, leftPlayerInitialX, rightPlayerInitialX } from './gameSettings'
import getNewVel from './getNewVel/getNewVel'

let state = { myself: {}, enemy: {}, ball: {} }

export default {


  setMyInitialPosition(sideToPlay) {
    // if server didn't tell the side,
    // choose it in modal window
    // then calculate inital position
    // and resolve promise to start drawing

    const position = sideToPlay === 'left' ?
      [leftPlayerInitialX, 0] :
      [rightPlayerInitialX, 0]

    this.applyNewData({
      myself: {
        sideToPlay,
        position,
        velocity: [0, 0]
      }
    })
  },


  applyEnemyData(data) {
    state.enemy.position = [
      canvasSize.width / 100 * data.playerPositionPercentage[0],
      canvasSize.height / 100 * data.playerPositionPercentage[1] 
    ]
  },

  createMessageToEnemy() {
    const { position } = state.myself
    return {
      playerPositionPercentage: [
        (position[0] / canvasSize.width * 100).toFixed(4),
        (position[1] / canvasSize.height * 100).toFixed(4)
      ],
      // ballPosition: state.ball.position
    }
  },


  startJumping() {
    // not a single jump, but continuous jumping while 'up' pressed
    state.jumping = true
  },

  stopJumping() {
    state.jumping = false
  },

  startMovingLeft() {
    state.movingLeft = true
  },
  stopMovingLeft() {
    state.movingLeft = false
  },
  startMovingRight() {
    state.movingRight = true
  },
  stopMovingRight() {
    state.movingRight = false
  },


  updateMyPosition() {
    state.myself.velocity = getNewVel({...state})
    state.myself.position[0] += state.myself.velocity[0]
    state.myself.position[1] += state.myself.velocity[1]
  },


  applyNewData(newData) {
    state = {
      ...state,
      ...newData
    }
  },


  getState() {
    return state;
  }
}


