const width = window.innerWidth > 880 ? 800 : window.innerWidth / 10 * 9
const height = width / 2.5

const canvasSize = {
  width,
  height
}

const playerRadius = canvasSize.width / 30

const abyssWidth = canvasSize.width / 2

module.exports = {

  abyssWidth,

  leftFenceXPosition: (canvasSize.width - abyssWidth) / 2,

  rightFenceXPosition: canvasSize.width - (canvasSize.width - abyssWidth) / 2,

  leftPlayerInitialX: (canvasSize.width - abyssWidth) / 4,

  rightPlayerInitialX: canvasSize.width - (canvasSize.width - abyssWidth) / 4,

  canvasSize,

  ballRadius: canvasSize.height / 10,

  playerRadius,

  xDamping: canvasSize.width / 500,

  xAcceleration: canvasSize.width / 800, // increase of hor speed while L/R key pressed
 
  maxPlayerXVel: canvasSize.width / 100, // xAcceleration is limited by this

  gravity: canvasSize.height / 500,

  yDampingOnRaise: 0.97,

  yAccelerationOnFall: 1.04,

  jumpingImpulse: canvasSize.height / 22,

  playerFlatteningOnFall: canvasSize.height / 40

}



