import { playerRadius, canvasSize, maxPlayerXVel, leftFenceXPosition, rightFenceXPosition, xDamping, xAcceleration } from '../gameSettings'

let playerLeftLimit, playerRightLimit

export default function({
  movingRight,
  movingLeft,
  myself: { sideToPlay, position: [xPos], velocity: [xVel] },
}) {

  defineLimits(sideToPlay)

// accelerate to right
  if (movingRight && xVel < maxPlayerXVel) {
    xVel += xAcceleration
  }

// 2. moving to right
  if (xVel > 0) {

  // 2.1. stop if reached right wall
    if (xPos >= playerRightLimit) {
      xVel = 0
    }

  // 2.2. smoothly brake if right button was released
    if (!movingRight && xVel >= xDamping) {
      xVel -= xDamping
    }

  // 2.3. while braking, if vel is too small, than stop
    if (!movingRight && xVel < xDamping) {
      xVel = 0
    }

  // 2.4. prevent falling out of right border - set vel to remaining (very small) space
    const spaceBeforeRightLimit = playerRightLimit - xPos
    if (spaceBeforeRightLimit < xVel) {
      xVel = spaceBeforeRightLimit
    }
  }


// accelerate to left
  if (movingLeft && xVel > -maxPlayerXVel) {
    xVel -= xAcceleration
  }

// 3. moving to left
  if (xVel < 0) {

  // 3.1. stop if reached left wall
    if (xPos < playerLeftLimit) {
      xVel = 0
    }

  // 3.2. smoothly brake if left button was released
    if (!movingLeft && xVel <= -xDamping) {
      xVel += xDamping
    }

  // 3.3. while braking, if vel is too small, than stop
    if (!movingLeft && xVel > -xDamping) {
      xVel = 0
    }

  // 3.4. prevent falling out of left border - set vel to remaining (very small) space
    const spaceBeforeLeftLimit = xPos - playerLeftLimit
    if (spaceBeforeLeftLimit + xVel <= 0) {
      xVel = -spaceBeforeLeftLimit
    }

  }

  return parseFloat(xVel.toFixed(3))
}


function defineLimits(sideToPlay) {
  if (!playerLeftLimit) {
    playerLeftLimit = sideToPlay === 'left' ?
    playerRadius :
    (rightFenceXPosition + playerRadius)
  }
  if (!playerRightLimit) {
    playerRightLimit = sideToPlay === 'left' ?
    (leftFenceXPosition - playerRadius) :
    (canvasSize.width - playerRadius)
  }
}


